import fetch from '@/utils/fetch'

export default {
  getNewsTye() { // 
    return [{ value: 10, label: '' }, { value: 20, label: '' }, { value: 30, label: '' }, { value: 40, label: '' }, { value: 50, label: '' }]
  },
  getNewsList(searchInfo) {
    return fetch({ // 
      url: 'http://getNewsList',
      params: searchInfo,
      method: 'get'
    })
  },
  getNewsDetail(searchInfo) {
    return fetch({ // 
      url: 'http://getNewsDetail',
      params: searchInfo,
      method: 'get'
    })
  },
  getNewsAuthors() {
    return fetch({ // 
      url: 'http://getNewsAuthors',
      params: { v: new Date() },
      method: 'get'
    })
  },
  getNewsPublishUsers() {
    return fetch({ // 
      url: 'http://getNewsPublishUsers',
      params: { v: new Date() },
      method: 'get'
    })
  },
  getNewsTitles() {
    return fetch({ // 
      url: 'http://getNewsTitles',
      params: { v: new Date() },
      method: 'get'
    })
  }
}



