import router from '../router'
import store from '../store'
import auth from '../utils/auth'

const whitelist = ['/', '/401', '/404', '/error'] // 
const cmsbpwhitelist = ['/cms/bp/regist', '/cms/bp/login'] // cms
router.beforeEach((to, from, next) => {
  if (to.path.includes('/cms/')) { // cms
    if (to.path.includes('/cms/fp/')) { // cms
      next()
    } else if (to.path.includes('/cms/bp/')) { // cms
      if (cmsbpwhitelist.includes(to.path)) { // cms
        next()
      } else {
        if (auth.getCMSToken()) { // 
          if (store.getters.cmsUser.ID === -1) { // 
            store.dispatch('GetUserInfoCMS').then(r => { // 
              next()
            }).catch(e => {
              next({ path: '/error', query: { msg: '' }})
            })
          } else {
            next()
          }
        } else {
          next({ path: '/cms/bp/login' })
        }
      }
    } else {
      next({ path: '/404' })
    }
  } else {
    if (whitelist.includes(to.path)) {
      next()
    } else {
      next({ path: '/404' })
    }
  }
})



