<template>
  <div class="detail" v-loading="loading1">
    <h1>{{detail.title}}</h1>
    <div class="info">
      <span>{{detail.ID}} </span>
      <span>{{detail.newsStatusDes}} </span>
      <span>{{detail.type}} </span>
      <span>{{detail.publishUser}} </span>
      <span>{{detail.publishTime}} </span>
      <span>{{detail.readCount}} </span>
    </div>
    <div class="content">{{detail.content}}</div>
    <div class="foot">
      <span>{{detail.author}}</span>
      <br/>
      <span>{{detail.insertTime}}</span>
      <br/>
      <span>{{detail.updateTime}}</span>
    </div>
  </div>
</template>

<script>
import { Message } from 'element-ui'
import cmsAPI from '@/modules/cms/api/newsAPI'
export default {
  name: 'detail',
  data() {
    return {
      detail: {
        ID: 0, // ID
        title: '', // 
        type: '', // 
        content: '', // 
        author: '', // 
        newsStatus: 0, // 0-;1-;9-
        newsStatusDes: '', // 
        insertUserID: '', // ID
        insertTime: '', // 
        updateTime: '', // 
        readCount: 0, // 
        publishUserID: -1, // ID
        publishUser: '', // 
        publishTime: '' // 
      },
      loading1: true
    }
  },
  created() {
    const detailRes = cmsAPI.getNewsDetail(this.$route.query.ID)
    this.loading1 = false
    if (detailRes.IsSuccess) {
      this.detail = detailRes.Data
    } else {
      Message({
        type: 'error',
        message: detailRes.Msg,
        durarion: 3 * 1000
      })
    }
  }
}
</script>

<style scoped>
.detail {
  width: 80%;
  margin-top: 20px
}

.info {
  margin-top: 20px;
  line-height: 20px;
  font-size: 6px;
  color: #8c8c8c
}

.content {
  margin-top: 20px;
  position: relative;
  overflow: hidden;
  color: #3e3e3e;
  font-size: 20px
}

.foot {
  margin-top: 30px;
  font-size: 16px;
  color: #8c8c8c;
  float: right;
  right: 100px
}
</style>



