<template>
  <div class="detail">
    <el-form ref="form" :rules="rules" :model="addData" label-width="80px">
      <el-form-item label="" prop="title">
        <el-input v-model="addData.title" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="type">
        <el-input v-model="addData.type" placeholder="" size="small"></el-input>
      </el-form-item>
      <el-form-item label="" prop="content">
        <el-input v-model="addData.content" type="textarea" placeholder="" :rows="5"></el-input>
      </el-form-item>
      <el-form-item label="" prop="author">
        <el-input v-model="addData.author" placeholder="" size="small"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="doAdd()"></el-button>
        <el-button @click="doReset()"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { Message, MessageBox } from 'element-ui'
import cmsAPI from '@/modules/cms/api/newsAPI'
export default {
  name: 'add',
  data() {
    return {
      addData: {
        title: '', // 
        type: '', // 
        content: '', // 
        author: '' // 
      },
      rules: {
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 20, message: '1~20', trigger: 'blur' }
        ],
        type: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 5, message: '1~5', trigger: 'blur' }
        ],
        content: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 10000, message: '1~10000', trigger: 'blur' }
        ],
        author: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 10, message: '1~10', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    /**
     * 
     */
    doAdd() {
      this.$refs.form.validate((valide) => {
        if (valide) {
          const addRes = cmsAPI.addNews(this.addData)
          if (addRes.IsSuccess) {
            MessageBox.confirm('', '', {
              type: 'success',
              confirmButtonText: '',
              cancelButtonText: ''
            }).then(() => {
              this.$router.push({ path: '/cms/bp/news/list' })
            }).catch(() => {
              this.$router.push({ path: '/cms/bp/news/add' })
            })
          } else {
            Message({
              type: 'error',
              message: addRes.Msg,
              durarion: 3 * 1000
            })
          }
        }
      })
    },
    /**
     * 
     */
    doReset() {
      this.addData.title = ''
      this.addData.type = ''
      this.addData.content = ''
      this.addData.author = ''
    }
  }
}
</script>

<style scoped>
.detail {
  width: 80%;
  margin-top: 20px;
  position: relative;
  overflow: hidden;
  color: #8c8c8c;
  font-size: 20px
}
</style>



