<template>
  <div class="page-container textalign">
    <div class="filter-container">
      <el-form :inline="true" :model="searchData" class="demo-form-inline">
        <el-form-item label="">
          <el-input v-model="searchData.ID" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="searchData.type" placeholder="" style="width:130px;">
            <el-option v-for="item in types" :label="item.label" :value="item.value" :key="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="searchData.author" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="searchData.publishUserID" placeholder="" style="width:130px;">
            <el-option v-for="item in publishUsers" :label="item.label" :value="item.value" :key="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="searchData.newsStatus" placeholder="" style="width:130px;">
            <el-option v-for="item in statuss" :label="item.label" :value="item.value" :key="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=''>
          <el-date-picker v-model='searchData.startTime' align='right' type='date' placeholder='--' style='width:120px;'> </el-date-picker>
          
          <el-date-picker v-model='searchData.endTime' align='right' type='date' placeholder='--' style='width:120px;'> </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click.stop.prevent="getTableData"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button>
            <router-link to="/cms/bp/news/add"></router-link>
          </el-button>
        </el-form-item>
        <table-head-select :selectableFields="selectableFields" @handleTableHeadChange="tableHeadChange"></table-head-select>
      </el-form>
    </div>
    <div class="table-container">
      <el-table :data='tableData' border style='width:100%' v-loading="loading1" element-loading-text="">
        <el-table-column :key='fieldInfo.field' v-for='(fieldInfo,index) in lastFields' :label="fieldInfo.label">
          <template scope="scope">
            <router-link :to="{path: '/cms/bp/news/detail', query: { ID: scope.row['ID'] }}" v-if="fieldInfo.field==='title'">{{scope.row[fieldInfo.field]}}</router-link>
            <span v-else>{{scope.row[fieldInfo.field]}}</span>
          </template>
        </el-table-column>
        <el-table-column width="130px" align="center" label="">
          <template scope="scope">
            <el-button size="mini" type="info" v-if="scope.row['newsStatus']===0" @click="doOperateNews(scope.row['ID'],'publish')"></el-button>
            <el-button size="mini" type="info" v-if="scope.row['newsStatus']===1" @click="doOperateNews(scope.row['ID'],'callBackPublish')"></el-button>
            <el-button size="mini" type="info" v-if="scope.row['newsStatus']!==9" @click="doOperateNews(scope.row['ID'],'delete')"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination-container">
      <el-pagination @current-change="handleCurrentChange" :current-page.sync="searchData.pageIndex" :page-size="searchData.pageSize" layout="total, prev, pager, next, jumper" :total="totalCount">
      </el-pagination>
    </div>

  </div>
</template>

<script>
import { Message } from 'element-ui'
import cmsAPI from '@/modules/cms/api/newsAPI'
import tableHeadSelect from '@/components/tableHeadSelect'
const totalFields = [
  { field: 'ID', label: '' },
  { field: 'title', label: '' },
  { field: 'type', label: '' },
  { field: 'author', label: '' },
  { field: 'publishTime', label: '' },
  { field: 'publishUser', label: '' },
  { field: 'readCount', label: '' },
  { field: 'newsStatusDes', label: '' }
]
export default {
  name: 'newsList',
  data() {
    return {
      types: [{ value: '', label: '' }], // 
      publishUsers: [{ value: -2, label: '' }], // 
      statuss: [{ value: -1, label: '' }, { value: 0, label: '' }, { value: 1, label: '' }, { value: 9, label: '' }], // 
      searchData: {
        ID: '', // 
        type: '', // 
        author: '', // 
        newsStatus: -1, // 
        publishUserID: -2, // 
        startTime: '', // 
        endTime: '', // 
        pageIndex: 1,
        pageSize: 15
      },
      defaultFields: totalFields.slice(0, 1), //  
      selectableFields: totalFields.slice(1), // 
      lastFields: totalFields, // 
      tableData: [], // 
      totalCount: 0, // 
      loading1: true // 
    }
  },
  methods: {
    getTableData() { // 
      this.loading1 = true
      this.searchData.pageIndex = this.searchData.pageIndex < 1 ? 1 : this.searchData.pageIndex
      const listRes = cmsAPI.getNewsList(this.searchData)
      this.loading1 = false
      if (listRes.IsSuccess) {
        this.tableData = listRes.Data.data
        this.totalCount = listRes.Data.totalCount
      } else {
        Message({
          type: 'error',
          message: listRes.Msg,
          durarion: 3 * 1000
        })
      }
    },
    handleCurrentChange(currentPage) { // 
      this.searchData.pageIndex = currentPage
      this.getTableData()
    },
    tableHeadChange(selectedFields) { // 
      this.lastFields = []
      this.lastFields.push(...this.defaultFields)
      const realSelectedFields = totalFields.filter(i => selectedFields.indexOf(i.field) >= 0)
      this.lastFields.push(...realSelectedFields)
    },
    doOperateNews(id, type) { // 
      const operateRes = cmsAPI.operateNews({ id: id, type: type })
      if (operateRes.IsSuccess) {
        this.getTableData()
      } else {
        Message({
          type: 'error',
          message: operateRes.Msg,
          durarion: 3 * 1000
        })
      }
    },
    doCBPublish(id) { // 
      Message({
        type: 'success',
        message: id + '',
        durarion: 3 * 1000
      })
    },
    doDelete(id) { // 
      Message({
        type: 'success',
        message: id + '',
        durarion: 3 * 1000
      })
    }
  },
  created() {
    const typeRes = cmsAPI.getDistinctInfos({ type: 'type', label: '' })
    if (typeRes.IsSuccess) {
      typeRes.Data.forEach(x => {
        this.types.push({ value: x, label: x })
      })
    } else {
      Message({
        type: 'error',
        message: typeRes.Msg,
        durarion: 3 * 1000
      })
    }
    const puRes = cmsAPI.getDistinctInfos({ type: 'publishUser', label: '' })
    if (puRes.IsSuccess) {
      puRes.Data.forEach(x => {
        this.publishUsers.push({ value: x.ID, label: x.name })
      })
    } else {
      Message({
        type: 'error',
        message: puRes.Msg,
        durarion: 3 * 1000
      })
    }
    this.searchData.ID = this.$route.query.ID
    this.searchData.author = this.$route.query.author
    this.searchData.publishUserID = this.$route.query.publishUserID
    this.searchData.type = this.$route.query.type
    this.getTableData()
  },
  components: {
    tableHeadSelect
  }
}
</script>
<style>
.page-container.textalign {
  text-align: justify
}
</style>



