import layout from './layouts/layout.vue'
import home from './home/index.vue'
import list from './news/list.vue'
import detail from './news/detail.vue'
import about from './about/index.vue'

export default [{
  path: '/cms',
  name: 'cms',
  redirect: '/cms/fp/home/index',
  component: layout
},
{
  path: '/cms/fp',
  name: 'cms',
  redirect: '/cms/fp/home/index',
  component: layout
},
{
  path: '/cms/fp/home',
  name: 'cmsHome',
  redirect: '/cms/fp/home/index',
  component: layout,
  children: [{ path: 'index', component: home, name: 'cmsHomeIndex' }]
},
{
  path: '/cms/fp/news',
  redirect: '/cms/fp/news/list',
  component: layout,
  name: '',
  children: [{ path: 'list', component: list, name: '' },
    { path: 'detail', component: detail, name: '' }
  ]
},
{
  path: '/cms/fp/about',
  redirect: '/cms/fp/about/index',
  component: layout,
  name: '',
  children: [{ path: 'index', component: about, name: '' }]
}]



