import layout from './layouts/layout.vue'
import home from './home/index.vue'
import login from './login/index.vue'
import regist from './regist/index.vue'
import list from './news/list.vue'
import detail from './news/detail.vue'
import add from './news/add.vue'

export default [{
  path: '/cms/bp',
  name: '',
  redirect: '/cms/bp/home/index',
  component: layout
},
{
  path: '/cms/bp/home',
  name: 'cmsHome',
  redirect: '/cms/bp/home/index',
  component: layout,
  children: [{ path: 'index', component: home, name: '' }]
},
{
  path: '/cms/bp/news',
  redirect: '/cms/bp/news/list',
  component: layout,
  name: '',
  children: [{ path: 'list', component: list, name: '' },
    { path: 'detail', component: detail, name: '' },
    { path: 'add', component: add, name: '' }
  ]
},
{
  path: '/cms/bp/regist',
  component: regist,
  name: ''
},
{
  path: '/cms/bp/login',
  component: login,
  name: ''
}]



